import { Component, OnInit } from '@angular/core';
// import { LiffService } from './services/liff.service';

declare var liff: any;
const SERVICE_UUID = 'c4dd444d-6d46-47de-8b24-c3b70fbf8b31';
const LED_CHARACTERISTIC_UUID   = 'E9062E71-9E62-4BC6-B0D3-35CDCD9B027B';
const BTN_CHARACTERISTIC_UUID = '62FBD229-6EDD-4D1A-B554-5C4E1BB29169';
const ULTRA_BEFORE_CHARACTERISTIC_UUID = 'C7D372AE-F856-46e0-A21B-AB41F3434656';
const ULTRA_AFTER_CHARACTERISTIC_UUID = '6DB3E8CD-E398-4B9F-8FC2-5CAA140A87C2';


// PSDI Service UUID: Fixed value for Developer Trial
const PSDI_SERVICE_UUID = 'E625601E-9E55-4597-A598-76018A0D293D';
const PSDI_CHARACTERISTIC_UUID = '26E2B12B-85F0-4F3F-9FDD-91D114270E6E';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'LIFF Mock';
  messages: string;
  userProfile: any;
  statusBle: boolean;

  ultraDataBefore: any;
  ultraDataAfter: any;
  service: any;
  ultraDataMessage: string;

  characteristic: any;

  constructor() {
    this.messages = '';
    this.statusBle = true;
  }

  ngOnInit() {
    liff.init(
      () => this.initLineLiff(),
      error => alert('31' + JSON.stringify(error))
    );
  }

  async initLineLiff() {
    liff.initPlugins(['bluetooth']).then(() => {
      this.liffCheckAvailablityAndDo(() => this.liffRequestDevice());
    });
  }

  async liffCheckAvailablityAndDo(callbackIfAvailable) {
    liff.bluetooth.getAvailability().then(isAvailable => {
      if (isAvailable) {
        callbackIfAvailable();
      } else {
        setTimeout(
          () => this.liffCheckAvailablityAndDo(callbackIfAvailable),
          10000
        );
      }
    });
  }

  async liffRequestDevice() {
    liff.bluetooth.requestDevice().then(device => {
      this.liffConnectToDevice(device);
    });
  }

  async liffConnectToDevice(device) {
    device.gatt.connect().then(() => {
      device.gatt.getPrimaryService(SERVICE_UUID).then(service => {
        this.service = service;
        this.liffGetUserService(service);
        // this.liffGetUltraDataService(service);
      });
      device.gatt.getPrimaryService(PSDI_SERVICE_UUID).then(service => {
        this.liffGetPSDIService(service);
      });
    });

    const disconnectCallback = () => {
      device.removeEventListener('gattserverdisconnected', disconnectCallback);
      this.initLineLiff();
    };
    device.addEventListener('gattserverdisconnected', disconnectCallback);
  }

  async getUltraDataService(device) {
    device.gatt.getPrimaryService(SERVICE_UUID).then(service => {
      this.liffGetUltraBeforeDataService(service);
      this.liffGetUltraAfterDataService(service);
    });
  }

  async liffGetUserService(service) {
    service.getCharacteristic(BTN_CHARACTERISTIC_UUID).then(characteristic => {
      this.liffGetButtonStateCharacteristic(characteristic);
    });
  }

  async liffGetUltraBeforeDataService(service) {
    service.getCharacteristic(ULTRA_BEFORE_CHARACTERISTIC_UUID).then(characteristic => {
      return characteristic.readValue();
    })
    .then(value => {
      this.ultraDataBefore = new Uint8Array(value.buffer);
      // this.ultraDataMessage = String.fromCharCode.apply(null, this.ultraData);
      alert(this.ultraDataBefore);
      // liff.sendMessages([
      //   {
      //     type: 'text',
      //     text: this.ultraDataMessage
      //   },
      // ])
      // .then(() => {
      //   alert('message send');
      // })
      // .catch((err) => {
      //   alert('Message ERROR:' + err);
      // });
    }).catch(error => alert('Before Data ERROR: ' + error));
  }

  async liffGetUltraAfterDataService(service) {
    service.getCharacteristic(ULTRA_BEFORE_CHARACTERISTIC_UUID).then(characteristic => {
      return characteristic.readValue();
    })
    .then(value => {
      this.ultraDataAfter = new Uint8Array(value.buffer);
      alert(this.ultraDataAfter);
    }).catch(error => alert('After Data ERROR: ' + error));
  }


  async liffGetPSDIService(service) {
    service.getCharacteristic(PSDI_CHARACTERISTIC_UUID).then(characteristic => {
      return characteristic.readValue();
    })
    .then(value => {
      const psdi = new Uint8Array(value.buffer);
    }).catch(error => alert('liffGetPSDIService ERROR: ' + error));
  }

  async liffGetButtonStateCharacteristic(characteristic) {
    characteristic.startNotifications().then(() => {
      characteristic.addEventListener('characteristicvaluechanged', e => {
        const val = new Uint8Array(e.target.value.buffer)[0];
        alert(`Button Val: ${val}`);
      });
    })
    .catch(error => {
      // uiStatusError(makeErrorMsg(error), false);
    });
  }

  test() {
    this.liffGetUltraBeforeDataService(this.service);
    this.liffGetUltraAfterDataService(this.service);
  }
}
